.navbar {
  position: fixed;
  max-width: 18.75rem;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #fff;
  padding: 1.25rem;
  z-index: 55;
  transform: translateX(-100%);
  transition: all 0.3s ease;
  overflow-y: auto;
}
.navbar__active {
  transform: translate(0);
}
.navbar .menu__logo {
  display: inline-flex;
}
.navbar .logo__text {
  display: block;
}
.navbar__top {
  position: relative;
}
.navbar__close {
  position: absolute;
  right: 0.9375rem;
  top: 0.4375rem;
  font-size: 1.5625rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar__body {
  margin-top: 2.1875rem;
}
.navbar__enter {
  display: flex;
  text-align: center;
  justify-content: center;
}
.navbar__enter a {
  text-align: center;
  padding: 0 0.625rem;
  font-size: 1.25rem;
  line-height: 100%; /* 20/20 */
}
.navbar__enter a:first-child {
  border-right: 2px solid #e2e2e2;
}
.navbar__btn {
  margin-top: 1.25rem;
}
.navbar__list {
  margin-top: 2.1875rem;
  display: flex;
  flex-direction: column;
  row-gap: 0.625rem;
}
.navbar__link {
  font-size: 1.0625rem;
  transition: all 0.2s ease;
}
@media (any-hover: hover) {
  .navbar__link:hover {
    color: #60c403;
  }
}

.layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4509803922);
  z-index: 54;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  overflow: hidden;
}
.layer__active {
  opacity: 1;
  visibility: visible;
}