* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

html,
body {
  min-width: 320px;
}

body {
  height: 100vh;
  color: #000;
  line-height: 1;
  font-family: "Rubik";
  font-size: 0.9375rem;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (max-width: 47.99875em) {
  body {
    padding-top: 3.875rem;
  }
}

input,
button,
textarea {
  font-family: "Rubik";
  font-size: inherit;
}

input:focus {
  outline: red;
}

button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
}

a {
  color: inherit;
}

a:link,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
}

.contain {
  -o-object-fit: contain;
     object-fit: contain;
}

::-webkit-scrollbar {
  width: 0.3125rem;
  height: 0.1875rem;
}

::-webkit-scrollbar-thumb {
  background-color: #60c403;
}

::-webkit-scrollbar-track {
  background-color: #e2e2e2;
}

@font-face {
  font-family: Rubik;
  src: url("../../fonts/Rubik/Rubik-Regular.ttf") format("truetype");
  font-weight: 400;
}
@font-face {
  font-family: Rubik;
  src: url("../../fonts/Rubik/Rubik-Italic.ttf") format("truetype");
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: Rubik;
  src: url("../../fonts/Rubik/Rubik-Bold.ttf") format("truetype");
  font-weight: 700;
}
@font-face {
  font-family: Rubik;
  src: url("../../fonts/Rubik/Rubik-BoldItalic.ttf") format("truetype");
  font-weight: 400;
  font-style: italic;
}
[class*=__container] {
  max-width: 73.125rem;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 0.9375rem;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}

#root {
  height: 100%;
}

main {
  flex: 1 1 auto;
}

footer {
  flex: 0 0 auto;
}

.no-scroll {
  overflow: hidden;
}

.menu__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo__img {
  -o-object-fit: contain;
     object-fit: contain;
}
@supports (width: clamp( 1.875rem , 1.58044919rem  +  1.4727540501vw , 2.5rem )) {
  .logo__img {
    width: clamp( 1.875rem , 1.58044919rem  +  1.4727540501vw , 2.5rem );
  }
}
@supports not (width: clamp( 1.875rem , 1.58044919rem  +  1.4727540501vw , 2.5rem )) {
  .logo__img {
    width: calc(1.875rem + 0.625 * (100vw - 20rem) / 42.4375);
  }
}
@supports (height: clamp( 1.875rem , 1.58044919rem  +  1.4727540501vw , 2.5rem )) {
  .logo__img {
    height: clamp( 1.875rem , 1.58044919rem  +  1.4727540501vw , 2.5rem );
  }
}
@supports not (height: clamp( 1.875rem , 1.58044919rem  +  1.4727540501vw , 2.5rem )) {
  .logo__img {
    height: calc(1.875rem + 0.625 * (100vw - 20rem) / 42.4375);
  }
}

.logo__text {
  font-size: 0.875rem;
  text-align: center;
  font-weight: 700;
  color: #60c403;
}
@media (max-width: 47.99875em) {
  .logo__text {
    display: none;
  }
}

.all__categoties {
  padding: 0.5625rem 0.3125rem;
  width: 100%;
  text-align: center;
  background-color: #60c403;
  color: #fff;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  -moz-column-gap: 0.3125rem;
       column-gap: 0.3125rem;
}
@media (any-hover: hover) {
  .all__categoties:hover {
    background-color: #6bd804;
  }
}
.all__categoties svg {
  width: 1.25rem;
  height: 1.25rem;
  color: #fff;
}

.scroll-btn {
  display: none;
}
@media (max-width: 47.99875em) {
  .scroll-btn {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 0.625rem;
    bottom: 0.625rem;
    transform: translateY(0.625rem);
    opacity: 0;
    transition: all 0.3s ease;
    background-color: #fff;
    border-radius: 50%;
    z-index: 50;
  }
  .scroll-btn.active {
    opacity: 1;
    transform: translate(0);
  }
  .scroll-btn svg {
    font-size: 3.125rem;
    color: #60c403;
    transition: all 0.3s ease;
  }
}
@media (max-width: 47.99875em) and (any-hover: hover) {
  .scroll-btn svg:hover {
    color: #6bd804;
  }
}

.btn__buy {
  color: #fff;
  background-color: #d30008;
  padding: 0.3125rem;
  transition: all 0.2s ease;
  text-align: center;
}
@media (any-hover: hover) {
  .btn__buy:hover {
    background-color: #ab080d;
  }
}

.btn {
  width: 100%;
  color: #fff;
  background-color: #60c403;
  padding: 0.8125rem;
  transition: all 0.2s ease;
  text-align: center;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: #6bd804;
  }
}

.title {
  font-weight: 700;
  color: #161616;
  padding: 0 80px 15px 0;
  display: inline-block;
  margin-bottom: 1.25rem;
  position: relative;
}
@supports (font-size: clamp( 1.0625rem , 0.8835290828rem  +  0.8948545861vw , 1.3125rem )) {
  .title {
    font-size: clamp( 1.0625rem , 0.8835290828rem  +  0.8948545861vw , 1.3125rem );
  }
}
@supports not (font-size: clamp( 1.0625rem , 0.8835290828rem  +  0.8948545861vw , 1.3125rem )) {
  .title {
    font-size: calc(1.0625rem + 0.25 * (100vw - 20rem) / 27.9375);
  }
}
.title::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.0625rem;
  background-color: #b0b0b0;
}
@media (max-width: 47.99875em) {
  .title::before {
    width: 50%;
  }
}