.footer-distributed {
  border-top: 0.0625rem solid #e2e2e2;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  padding: 55px 50px;
  margin-top: 2.1875rem;
}

@media (min-width: 29.99875em) {
  .footer-distributed .menu__logo {
    display: inline-flex;
  }
}

@media (max-width: 47.99875em) {
  .footer-distributed .logo__text {
    display: block;
  }
}

.footer-left,
.footer-center,
.footer-right {
  display: inline-block;
  vertical-align: top;
}

.footer-left {
  width: 40%;
}

.footer-links {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 61.99875em) {
  .footer-links {
    display: inline-flex;
  }
}

.footer-links a {
  flex: 0 0 auto;
  display: inline-block;
  line-height: 1.8;
  font-weight: 400;
  text-decoration: none;
  color: inherit;
}
@media (any-hover: hover) {
  .footer-links a:hover {
    text-decoration: underline;
  }
}

.footer-company-name {
  color: #acafb4;
  font-size: 14px;
  font-weight: normal;
}

.footer-center {
  width: 35%;
}

.footer-center p {
  display: inline-block;
  font-weight: 400;
  vertical-align: middle;
  margin: 0;
}

.footer-center p span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 2;
}

.footer__box {
  display: flex;
  align-items: center;
  -moz-column-gap: 0.625rem;
       column-gap: 0.625rem;
}
.footer__box:not(:last-child) {
  margin-bottom: 0.625rem;
}

.footer__box > a {
  text-align: left;
}
@media (any-hover: hover) {
  .footer__box > a:hover {
    text-decoration: underline;
  }
}

.footer__box p a {
  text-align: left;
  white-space: nowrap;
  color: #60c403;
}
@media (any-hover: hover) {
  .footer__box p a:hover {
    text-decoration: underline;
  }
}

.footer__num {
  transition: all 0.2s ease;
}

.footer-right {
  width: 20%;
}

.footer-company-about {
  margin-top: 1.25rem;
  margin-bottom: 0.625rem;
  line-height: 20px;
  color: #000;
  font-size: 13px;
  font-weight: normal;
}

.footer-company-about span {
  display: block;
  font-size: 14px;
  font-weight: 700;
}

.footer-icons {
  margin-top: 25px;
  display: flex;
  gap: 0.1875rem;
}

.footer-icons a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;
  transition: all 0.2s ease;
}
@media (any-hover: hover) {
  .footer-icons a:hover {
    background-color: #4d5357;
  }
}

@media (max-width: 61.99875em) {
  .footer-distributed {
    font: bold 14px sans-serif;
  }
  .footer-left,
.footer-center,
.footer-distributed,
.footer-company-name {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }
  .footer-distributed {
    margin-bottom: 0;
  }
  .footer-right {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .footer-icons {
    justify-content: center;
  }
  .footer-company-name {
    margin-top: 1.25rem;
    margin-bottom: 0;
  }
  .footer-center i {
    margin-left: 0;
  }
}
@media (max-width: 47.99875em) {
  .footer-center {
    display: none;
  }
}